import React,{Component} from 'react';
import './index.less'
import { Modal, Row, Col, Form, Input, Checkbox} from "antd";
const FormItem = Form.Item;

class setRoleModal extends Component{
    constructor(){
        super();
        this.checkedList = [{
            line: 1,
            childrenList: [{
                title: '',
                dataIndex: 'allowQuery',
            },{
                title: '',
                dataIndex: 'allowRead',
            },{
                title: '',
                dataIndex: 'allowEdit',
            }]
        },{
            line: 2,
            childrenList: [{
                title: '',
                dataIndex: 'allowDelete',
            },{
                title: '',
                dataIndex: 'allowBad',
            },{
                title: '',
                dataIndex: 'allowUpload',
            }]
        },{
            line: 3,
            childrenList: [{
                title: '',
                dataIndex: 'allowDown',
            },{
                title: '',
                dataIndex: 'allowAudit',
            },{
                title: '',
                dataIndex: 'allowApprove',
            }]
        },{
            line: 4,
            childrenList: [{
                title: '',
                dataIndex: 'allowReceive',
            },{
                title: '',
                dataIndex: 'allowAuthorization',
            },{
                title: '',
                dataIndex: 'allowPrint',
            }]
        },{
            line: 5,
            childrenList: [{
                title: '',
                dataIndex: 'allowArchive',
            },{
                title: '',
                dataIndex: 'allowConfigure',
            },{
                title: '',
                dataIndex: 'allowEnable',
            }]
        },{
            line: 6,
            childrenList: [{
                title: '',
                dataIndex: 'allowFreeze',
            },{
                title: '',
                dataIndex: 'allowYear',
            },{
                title: '',
                dataIndex: 'allowDevice',
            }]
        }]
    }
    // 
    handleaddsetMeal = (e) => {
        e.preventDefault();
        const { form, type } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                values.type = type;
                this.props.handleaddsetMeal(values)
            }
        })
    }
    // 
    handleCancel = () => {
        this.props.handleCancel();
    }
    render(){
        const { setRoleModalShow, form, type, roleDetail } = this.props;
        const { getFieldDecorator } = form;
        return(
            <Modal
                className="setRoleModal"
                width={800}
                title={type === 0 ? '' : ''}
                visible={setRoleModalShow}
                onCancel={this.handleCancel}
                onOk={this.handleaddsetMeal}
            >
                <div className="setRole-box">
                    <Form labelCol={{ span: 8 }} wrapperCol={{ span: 11 }}>
                        <FormItem 
                            label="">
                            {getFieldDecorator('roleName', {
                                initialValue: roleDetail.roleName,
                                rules: [
                                    { required: true, message: '' },
                                ],
                            })(
                                <Input placeholder="" />
                            )}
                        </FormItem>
                        {
                            this.checkedList.map((item,index) => {
                                return (<Row gutter={24} key={index}>
                                    {
                                        item.childrenList.map((li,idx) => {
                                            return (
                                                <Col span={8} key={idx}>
                                                    <FormItem label={li.title}>
                                                        {getFieldDecorator(li.dataIndex, {
                                                            valuePropName: 'checked',
                                                            initialValue: roleDetail[li.dataIndex] ? true : false
                                                        })(
                                                            <Checkbox>{li.title}</Checkbox>
                                                        )}
                                                    </FormItem>
                                                </Col>
                                                
                                            )
                                        })
                                    }
                                </Row>)
                            })
                        }
                        <Row gutter={24}>
                            <Col span={8}>
                                <FormItem 
                                    label="">
                                    {getFieldDecorator('allowDownSourceFile', {
                                        valuePropName: 'checked',
                                        initialValue: roleDetail.allowDownSourceFile ? true : false
                                    })(
                                        <Checkbox></Checkbox>
                                    )}
                                </FormItem>
                            </Col>
                            <Col span={8}>
                                <FormItem  label="">
                                    {getFieldDecorator('sortCode', {
                                        initialValue: roleDetail.sortCode,
                                        rules: [
                                            { required: true, message: '' },
                                            { pattern: /^\d{1,10}$/, message: '' }
                                        ],
                                    })(
                                        <Input placeholder="" />
                                    )}
                                </FormItem>
                            </Col>
                        </Row>
                    </Form>
                </div>
            </Modal>
        )
    }
}
export default Form.create()(setRoleModal)