import React,{Component} from 'react';
import './index.less'
import { Modal, Form, Input, Checkbox, TreeSelect } from "antd";
const FormItem = Form.Item;
const { TreeNode } = TreeSelect;

class setUserModal extends Component{
    // 
    handleaddsetMeal = (e) => {
        e.preventDefault();
        const { form, type } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                values.type = type;
                console.log(values)
                this.props.handleaddsetMeal(values)
            }
        })
    }
    // 
    handleCancel = () => {
        this.props.handleCancel();
    }
    // 
    renderTreeNodes = (data) => {
        const treeItem = data.map(item => {
            if (item.children && item.children.length) {
                return <TreeNode value={item.bizId} title={item.deptName} key={item.bizId} dataRef={item}>
                    {this.renderTreeNodes(item.children)}
                </TreeNode>
            }
            return <TreeNode value={item.bizId} title={item.deptName} key={item.bizId} />;
        })
        return treeItem
    }
    // 
    renderRoleNodes = (data) => {
        const treeItem = data.map(item => {
            if (item.children && item.children.length) {
                return <TreeNode value={item.bizId} title={item.roleName} key={item.bizId} dataRef={item}>
                    {this.renderTreeNodes(item.children)}
                </TreeNode>
            }
            return <TreeNode value={item.bizId} title={item.roleName} key={item.bizId} />;
        })
        return treeItem
    }
    render(){
        const { setUserModalShow, form, type, userDetail, deptList, roleList } = this.props;
        const { getFieldDecorator } = form;
        return(
            <Modal
                className="setUserModal"
                title={type === 0 ? '' : ''}
                visible={setUserModalShow}
                width={500}
                onCancel={this.handleCancel}
                onOk={this.handleaddsetMeal}
            >
                <div className="setUser-box">
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 17 }}>
                        <FormItem 
                            label="">
                            {getFieldDecorator('account', {
                                initialValue: userDetail.account,
                                rules: [
                                    { required: true, message: '' },
                                ],
                            })(
                                <Input placeholder="" />
                            )}
                        </FormItem>
                        <FormItem 
                            label="">
                            {getFieldDecorator('dspName', {
                                initialValue: userDetail.dspName,
                                rules: [
                                    { required: true, message: '' },
                                ],
                            })(
                                <Input placeholder="" />
                            )}
                        </FormItem>
                        <FormItem 
                            label="">
                            {getFieldDecorator('deptId', {
                                initialValue: userDetail.deptId,
                                rules: [
                                    { required: true, message: '' },
                                ],
                            })(
                                <TreeSelect placeholder="" allowClear treeDefaultExpandAll>
                                    <TreeNode value={0} title="" key={0} />;
                                    {this.renderTreeNodes(deptList)}
                                </TreeSelect>
                            )}
                        </FormItem>
                        <FormItem 
                            label="">
                            {getFieldDecorator('roleId', {
                                initialValue: userDetail.roleId,
                                rules: [
                                    { required: true, message: '' },
                                ],
                            })(
                                <TreeSelect placeholder="" allowClear treeDefaultExpandAll>
                                    {this.renderRoleNodes(roleList)}
                                </TreeSelect>
                            )}
                        </FormItem>
                        <FormItem label="">
                            {getFieldDecorator('passWord', {
                                initialValue: userDetail.passWord,
                                rules: [
                                    { required: true, message: '' },
                                ],
                            })(
                                <Input type="password" placeholder="" />
                            )}
                        </FormItem>
                        <FormItem label="">
                            {getFieldDecorator('allowEnable', {
                                valuePropName: 'checked',
                                initialValue: userDetail['allowEnable'] ? true : false
                            })(
                                <Checkbox />
                            )}
                        </FormItem>
                        <FormItem label="">
                            {getFieldDecorator('email', {
                                initialValue: userDetail.email,
                                rules: [
                                    { required: true, message: '' },
                                    { type: 'email', message: '' }
                                ],
                            })(
                                <Input placeholder="" />
                            )}
                        </FormItem>
                        <FormItem label="">
                            {getFieldDecorator('wx', {
                                initialValue: userDetail.wx,
                                rules: [
                                    { required: false, message: '' },
                                ],
                            })(
                                <Input placeholder="" />
                            )}
                        </FormItem>
                        <FormItem label="">
                            {getFieldDecorator('mobile', {
                                initialValue: userDetail.mobile,
                                rules: [
                                    { required: false, message: '' },
                                    { pattern: /^1[34578]\d{9}$/, message: '' }
                                ],
                            })(
                                <Input placeholder="" />
                            )}
                        </FormItem>
                        <FormItem label="QQ">
                            {getFieldDecorator('qq', {
                                initialValue: userDetail.qq,
                                rules: [
                                    { required: false, message: '' },
                                ],
                            })(
                                <Input placeholder="" />
                            )}
                        </FormItem>
                        <FormItem 
                            label="">
                            {getFieldDecorator('sortCode', {
                                initialValue: userDetail.sortCode,
                                rules: [
                                    { required: true, message: '' },
                                    { pattern: /^\d{1,10}$/, message: '' }
                                ],
                            })(
                                <Input placeholder="" />
                            )}
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        )
    }
}
export default Form.create()(setUserModal)