import React,{ Component } from 'react'
import { Input, Form, Row, Col, Button, TreeSelect } from 'antd'
const FormItem = Form.Item;
const { TreeNode } = TreeSelect;

class FormListuser extends Component{
    // 
    handleSubmit = () => {
        this.props.form.validateFields((err,values)=>{
            if(err){
                return false
            }
            const data = {};
            // undefineditem
            Object.keys(values).forEach(res=>{
                if(values[res]){
                    data[res] = values[res]
                }
            })
            this.props.handleSearchBtn(data);
        })
    }
    handleReset = () => {
        this.props.form.resetFields();
        this.props.handleSearchBtn({});
    }
    // 
    renderTreeNodes = (data) => {
        const treeItem = data.map(item => {
            if (item.children && item.children.length) {
                return <TreeNode value={item.bizId} title={item.deptName} key={item.bizId} dataRef={item}>
                    {this.renderTreeNodes(item.children)}
                </TreeNode>
            }
            return <TreeNode value={item.bizId} title={item.deptName} key={item.bizId} />;
        })
        return treeItem
    }
    render(){
        const { getFieldDecorator } = this.props.form;
        return (
            <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                <Row gutter={24}>
                    <Col span={8}>
                        <FormItem label="/">
                            {
                                getFieldDecorator('account')(<Input placeholder="" autoComplete='off' />)
                            }
                        </FormItem>
                    </Col>
                    <Col span={8}>
                        <FormItem label="">
                            {
                                getFieldDecorator('deptId')(
                                    <TreeSelect placeholder="" allowClear treeDefaultExpandAll>
                                        {this.renderTreeNodes(this.props.deptList)}
                                    </TreeSelect>
                                )
                            }
                        </FormItem>
                    </Col>
                    <Col span={4}>
                        <Button onClick={this.handleReset} style={{ float: 'right' }}></Button>
                        <Button type="primary"  onClick={this.handleSubmit} style={{ marginRight: 20, float: 'right' }}></Button>
                    </Col>
                </Row>
            </Form>
        )
    }
}
export default Form.create()(FormListuser)

