import React, { Component } from 'react'
import { Button, message } from 'antd';
import './index.less'
import BreadcrumbLink from '../../../components/bread'
import Table from './table-list';
import MenuModal from './menu-modal';

class MenuManage extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '' },
                { name: '' }
            ],
            loading: false,   // 
            dataList: [],     // 
            type: 0,          //  0:1:
            menuDetail: {},    // role
            setMenuModalShow: false,    //  
        }
    }
    componentDidMount() {
        this.queryTableList();
    }
    // 
    changeData = (data) => {
        data.map(item => {
            if (item.list && item.list.length) {
                const val= item['list'];
                delete item['list'];
                item['children']=val;
                this.changeData(item.children);
            }
            return {...item}
        })
        return data
    }
    // 
    queryTableList = () =>{
        this.setState({
            loading: true,
        },() => {
            this.apis.listQMSMenus({parentId: 0},(res) => {
                if(res.code === '200') {
                    const list = this.changeData(res.data);
                    this.setState({
                        dataList: list,
                        loading: false,
                    })
                }else{
                    this.setState({
                        loading: false,
                    })
                }
            })
        })
    }
    //  
    handleDelete = (bizId) => {
        this.apis.deleteMenu({bizId},(res) => {
            if(res.code === '200') {
                message.success('');
                this.queryTableList();
            }
        })
    }
    // 
    handleEdit = (item) => {
        this.setState({
            setMenuModalShow:true,
            type: 1,
            menuDetail: item
        })
    }
    // 
    handleMenuModal = () => {
        this.setState({
            setMenuModalShow:true,
            type: 0,
            menuDetail: {}
        })
    }
    // 
    handleCancel = () => {
        this.setState({
            setMenuModalShow:false,
        },() => {
            this.refs.MenuModal.resetFields();
        })
    }
    // 
    handleaddsetMeal = (values) => {
        const params = {
            ...values
        }
        delete params['type'];
        if (values.type === 0) { // 
            this.apis.addMenu(params,(res) => {
                if(res.code === '200') {
                    message.success('');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }else{ // 
            params['bizId'] = this.state.menuDetail.bizId;
            this.apis.updateMenu(params,(res) => {
                if(res.code === '200') {
                    message.success('');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }
    }
    render() {
        const { breadcrumbData, loading, dataList, setMenuModalShow, type, menuDetail } = this.state;
        return (
            <div className="menuManage">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="btn">
                        <Button type="primary" onClick={this.handleMenuModal}></Button>
                    </div>
                    <div className="table-list">
                        <Table 
                            loading={loading} 
                            data={dataList}
                            childrenColumnName='list'
                            handleDelete={this.handleDelete}
                            handleEdit={this.handleEdit}
                        />
                    </div>
                </div>
                <MenuModal
                    ref="MenuModal"
                    setMenuModalShow={setMenuModalShow}
                    type={type}
                    menuDetail={menuDetail}
                    menuData={dataList}
                    handleCancel={this.handleCancel}
                    handleaddsetMeal={this.handleaddsetMeal}
                />
            </div>
        )
    }
}
export default MenuManage