import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Card, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../api/index'
import BreadcrumbLink from '../../components/bread'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import axios from 'axios'
import { searchToParams } from '../../utils/utils.js'

const { Option } = Select;
const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        const { isTest, isLook, isTestTwo } = searchToParams();
        this.state = {
            msg: "",
            isTest,
            isLook,
            lookData: [],
            data: {
                monDataList: [
                    {
                        questionA: "AA",
                        questionB: "BB",
                        questionC: "CC",
                        questionD: "DD",
                        questionE: "EE",
                        myAnsow: "A",
                        myType: "2",
                    },
                    {
                        questionA: "A",
                        questionB: "Bv",
                        questionC: "C",
                        questionD: "Dvg",
                        questionE: "E",
                        myAnsow: "C",
                        myType: "0",
                    }
                ]
            },
        }
    }
    componentDidMount() {
        this.timeTransition(20000);//
        this.viewMyTrain()
    }
    timeTransition = (ms) => {
        let maxtime = ms / 1000; //
        let timer = null;
        let _this = this;
        setTimeout(function f() {
            if (maxtime >= 0) {
                let minutes = Math.floor(maxtime / 60);
                let seconds = Math.floor(maxtime % 60);
                minutes < 10 ? minutes = '0' + minutes : minutes = minutes;
                seconds < 10 ? seconds = '0' + seconds : seconds = seconds;
                let msg = " <em>" + minutes + "" + seconds + "</em> ";
                _this.setState({
                    msg
                });
                --maxtime;
            } else {
                _this.setState({
                    msg: ''
                });
                clearTimeout(timer);
                return;
            }
            timer = setTimeout(f, 1000);
        }, 1000);
    }
    //
    handleLoginOut = () => {
        this.props.history.push('/system/myTraining')
    }
    // 
    handleConfirmation = () => {
        this.props.history.push('/system/myTraining')
    }
    viewMyTrain = () => {
        Api.viewMyTrain({
            userId: this.props.location.state.query.userId ? this.props.location.state.query.userId : []
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    lookData: res.data.list
                })
            }
        })
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            data,
            lookData,
            isTest, isLook
        } = this.state;
        console.log(lookData, "7777777777")
        let { bool, days, hours, minutes, seconds } = this.state
        const topic = lookData.map((val, i) => {
            return <Card title={<div></div>} style={{ width: 300, marginTop: '50px' }}>
                <p>{val.questionA}</p>
                <p>{val.questionB}</p>
                <p>{val.questionC}</p>
                <p>{val.questionD}</p>
                <p>{val.questionE}</p>
                {
                    isTest === "true" ?
                        <div>
                            <Select
                                showSearch
                                allowClear
                                style={{ width: "80px" }}
                                placeholder=""
                                optionFilterProp="children"
                                onChange={this.onChange}
                                // onFocus={onFocus}
                                // onBlur={onBlur}
                                // onSearch={onSearch}
                                filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                            >
                                <Option value="jack">A</Option>
                                <Option value="lucy">B</Option>
                                <Option value="Yiminghe">C</Option>
                                <Option value="Yiminghe">D</Option>
                                <Option value="Yiminghe">E</Option>
                            </Select>
                        </div>
                        :
                        null
                }
                {
                    isLook === "true" ?
                        <div>
                            <span>{val.myAnsow}</span>
                            <span style={{ marginLeft: '30px', }}>{val.myType}</span>
                        </div>
                        :
                        null
                }
            </Card>
        })


        const breadcrumbData = isTest === "true" ?
            [
                { name: '' },
                { name: '' },
                { name: '' }
            ]
            :
            isLook === "true" ?
                [
                    { name: '' },
                    { name: '' },
                    { name: '' }
                ]
                :
                    null

        return (
            <div className="startTheExam">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="information">
                        {
                            isTest === "true" ?
                                <div style={{ float: "right" }}>
                                    <span></span>
                                </div>
                                :
                                null
                        }
                        <div>
                            <span></span><span></span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span></span><span></span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span>90 </span>
                            <span style={{ marginLeft: '20px' }}> 100 </span>
                            <span style={{ marginLeft: '20px' }}> 60 </span>
                        </div>
                    </div>
                    <div className="examTitle">
                        <div style={{ marginLeft: '5px' }}>
                            <span> (2/)</span>
                        </div>
                    </div>
                    {topic}
                    <div style={{ position: "relative", top: "20px" }}>
                        <Button style={{ float: 'right' }} onClick={this.handleLoginOut}></Button>
                        {
                            isTest === "true" ?
                                <Button style={{ float: 'right', marginRight: '20px' }} type="primary" onClick={this.handleConfirmation}></Button>
                                :
                                null
                        }
                    </div>
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner