import React, { Component } from 'react'
import { Form, Input, Select, Tabs, Row, Col, message, Button } from 'antd'
import BreadcrumbLink from '../../../components/bread'
import './index.less'
import Api from '../../../api/index';
import { searchToParams } from '../../../utils/utils.js'
const { Option } = Select;
const { TextArea } = Input;
const { TabPane } = Tabs

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            const { bizId } = searchToParams();
            this.state = {
                bizId,
                selectByPrimaryKeyData: [],//
                pageListUserData: [],///
                listFileTypesData: [],//
                listAllApplyTypesData: [],//
                listApplyDeptInFileModelData: [],//
                listQMSDeptData: [],///
                writtenData: [],//
                listAllApplyReasonsData: [],//
                pageData: {
                    pageSize: 100,
                    page: 1
                },
                breadcrumbData: [
                    { name: '' },
                    { name: '' },
                    { name: '' }
                ],
            }
        }
        componentDidMount() {
            this.selectByPrimaryKey()//
            this.pageListUser()///
            this.listFileTypes()//
            this.listAllApplyTypes()//
            this.listApplyDeptInFileModel()//
            this.listQMSDept()///
            this.listUsers()//
            this.listAllApplyReasons()//
        }

        selectByPrimaryKey = () => {
            const { bizId } = this.state
            Api.selectByPrimaryKey({
                bizId: bizId,
            }, (res) => {
                if (res.code === '200') {
                    this.setState({
                        selectByPrimaryKeyData: res.data
                    })
                }
            })
        }

        ///
        pageListUser = () => {
            const { pageData } = this.state
            Api.pageListUser({
                pageSize: pageData.pageSize,
                pageNo: pageData.page
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        pageListUserData: res.data.list,
                    })
                }
            })
        }
        //
        listFileTypes = () => {
            Api.listFileTypes({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listFileTypesData: res.data.list,
                    })
                }
            })
        }
        //
        listAllApplyTypes = () => {
            Api.listAllApplyTypes({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listAllApplyTypesData: res.data.list,
                    })
                }
            })
        }
        //
        listApplyDeptInFileModel = () => {
            Api.listApplyDeptInFileModel({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listApplyDeptInFileModelData: res.data.list
                    })
                }
            })
        }
        ///
        listQMSDept = () => {
            Api.listQMSDept({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listQMSDeptData: res.data,
                    })
                }
            })
        }
        //
        listUsers = () => {
            Api.listUsers({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        writtenData: res.data.list,
                    })
                }
            })
        }
        //
        listAllApplyReasons = () => {
            Api.listAllApplyReasons({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listAllApplyReasonsData: res.data.list,
                    })
                }
            })
        }
        // 
        handleSearchPeople = () => {
            this.props.history.push('/fileManagement/application')
        }
        //
        handleReset = () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                console.log( values, 'AAAAAJAAAAAAAAA')
                const { selectByPrimaryKeyData } = this.state
                if (true) {
                    Api.addFileApply({
                        fileId: selectByPrimaryKeyData.bizId,
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        fileVersion: values.fileVersion == undefined ? values.fileVersion : values.fileVersion.trim(),
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                        effectDate: values.effectDate == undefined ? values.effectDate : values.effectDate.trim(),
                        applyDate: values.applyDate == undefined ? values.applyDate : values.applyDate.trim(),
                        content: values.content == undefined ? values.content : values.content.trim(),
                        auditUserId: values.auditUserId.key,
                        fileTypeId: values.fileTypeId.key,
                        approverUserId: values.approverUserId.key,
                        applyUserId: values.applyUserId.key,
                        applyType: values.applyType.key,
                        applyReasonId: values.applyReasonId.key,
                        applyDeptId: values.applyDeptId.key,
                        makeDeptId: values.makeDeptId.key,
                        submitType: 8,
                    }, (res) => {
                        if (res.code === '200') {
                            message.success(res.message)
                        }
                    })
                }
            });
        }

        render() {
            const {
                form,
            } = this.props;
            const {
                breadcrumbData,
                selectByPrimaryKeyData,
                pageListUserData,
                listFileTypesData,
                listAllApplyTypesData,
                listApplyDeptInFileModelData,
                listQMSDeptData,
                writtenData,
                listAllApplyReasonsData,
            } = this.state;

            ///
            const pageListUser = pageListUserData ? pageListUserData.map(res => {
                return (<Option key={res.bizId} values={res.bizId}>{res.dspName}</Option>)
            }) : null
            //
            const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
                return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
            }) : null
            //
            const listAllApplyTypes = listAllApplyTypesData ? listAllApplyTypesData.map(res => {
                return (<Option key={res.applyTypeId} values={res.applyTypeId}>{res.applyTypeName}</Option>)
            }) : null
            //
            const listApplyDeptInFileModel = listApplyDeptInFileModelData ? listApplyDeptInFileModelData.map(res => {
                return (<Option key={res.applyDeptId} values={res.applyDeptId}>{res.applyDeptName}</Option>)
            }) : null
            ///
            const listQMSDept = listQMSDeptData ? listQMSDeptData.map(res => {
                return (<Option key={res.bizId} values={res.bizId}>{res.deptName}</Option>)
            }) : null
            //
            const writtenBy = writtenData ? writtenData.map(res => {
                return (<Option key={res.userId} values={res.userId}>{res.userName}</Option>)
            }) : null
            //
            const listAllApply = listAllApplyReasonsData ? listAllApplyReasonsData.map(res => {
                return (<Option key={res.applyReasonId} values={res.applyReasonId}>{res.applyReasonName}</Option>)
            }) : null
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };

            return (
                <div className="applicationAmenndment">
                    <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                    <Form style={{ position: 'relative', top: '30px' }}>
                        <Row>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('fileNo', {
                                        initialValue: selectByPrimaryKeyData ? selectByPrimaryKeyData.fileNo : null
                                    })(
                                        <Input placeholder="" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label="/"
                                >
                                    {getFieldDecorator('fileVersion', {
                                        initialValue: selectByPrimaryKeyData ? selectByPrimaryKeyData.fileVersion : null
                                    })(
                                        <Input placeholder="" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('applyUserId', {
                                        initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.applyUserName, key: selectByPrimaryKeyData.applyUserId } : null,
                                    })(
                                        <Select
                                            disabled
                                            labelInValue
                                            allowClear
                                            placeholder=""
                                            onChange={this.queryEleList}
                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                        >
                                            {writtenBy}
                                        </Select>
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('fileName', {
                                        initialValue: selectByPrimaryKeyData ? selectByPrimaryKeyData.fileName : null
                                    })(
                                        <Input placeholder="" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('effectDate', {
                                        initialValue: selectByPrimaryKeyData ? selectByPrimaryKeyData.effectDate : null
                                    })(
                                        <Input placeholder="" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('auditUserId', {
                                        initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.approverUserName, key: selectByPrimaryKeyData.approverUserId } : null,
                                    })(
                                        <Select
                                            allowClear
                                            labelInValue
                                            placeholder=""
                                            onChange={this.queryEleList}
                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                        >
                                            {pageListUser}
                                        </Select>
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('fileTypeId', {
                                        initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.fileTypeName, key: selectByPrimaryKeyData.fileTypeId } : null,
                                    })(
                                        <Select
                                            allowClear
                                            labelInValue
                                            placeholder=""
                                            onChange={this.queryEleList}
                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                        >
                                            {listFileTypes}
                                        </Select>
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('applyDeptId', {
                                        initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.applyDeptName, key: selectByPrimaryKeyData.applyDeptId } : null,
                                    })(
                                        <Select
                                            allowClear
                                            labelInValue
                                            placeholder=""
                                            onChange={this.queryEleList}
                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                        >
                                            {listApplyDeptInFileModel}
                                        </Select>
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('approverUserId', {
                                        initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.auditorName, key: selectByPrimaryKeyData.auditorId } : null,
                                    })(
                                        <Select
                                            labelInValue
                                            allowClear
                                            placeholder=""
                                            onChange={this.queryEleList}
                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                        >
                                            {pageListUser}
                                        </Select>
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('applyType', {
                                        initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.applyTypeName, key: selectByPrimaryKeyData.applyType } : null,
                                    })(
                                        <Select
                                            labelInValue
                                            allowClear
                                            placeholder=""
                                            onChange={this.queryEleList}
                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                        >
                                            {listAllApplyTypes}
                                        </Select>
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('applyReasonId', {
                                        initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.applyReasonName, key: selectByPrimaryKeyData.applyReasonId } : null,
                                    })(
                                        <Select
                                            labelInValue
                                            allowClear
                                            placeholder=""
                                            onChange={this.queryEleList}
                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                        >
                                            {listAllApply}
                                        </Select>
                                    )}
                                </Form.Item>
                            </Col>
                            <Col span={8}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('makeDeptId', {
                                        initialValue: selectByPrimaryKeyData ? { label: selectByPrimaryKeyData.applyDeptName, key: selectByPrimaryKeyData.applyDeptId } : null,
                                    })(
                                        <Select
                                            labelInValue
                                            allowClear
                                            placeholder=""
                                            onChange={this.queryEleList}
                                            onFocus={this.onFocus}
                                            onBlur={this.onBlur}
                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                        >
                                            {listQMSDept}
                                        </Select>
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('applyDate', {
                                        initialValue: selectByPrimaryKeyData ? selectByPrimaryKeyData.applyDate : null
                                    })(
                                        <Input placeholder="" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={6}>
                                <Form.Item
                                    {...formItemLayout}
                                    label=""
                                >
                                    {getFieldDecorator('content', {
                                    })(
                                        <Input placeholder="" autoComplete='off' />
                                    )}
                                </Form.Item>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={6} offset={1}>
                                <Button onClick={this.handleReset} style={{}}></Button>
                                <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginLeft: 20, }}></Button>
                            </Col>
                        </Row>
                    </Form>
                </div>
            );
        }
    }
);
export default App