import React, { Component } from 'react'
import { Button, message } from 'antd';
import './index.less'
// import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
import SearchForm from "./search-form"; // 
import Table from "./table-list"; 
import UserModal from "./add-modal";
import NewPassword from './passWord-modal';
import RoleAuthorization from '../../../components/roleAuthorization'
import JobAuthorization from '../../../components/jobAuthorization'
import DeptModel from './dept-modal'
class userManage extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '' },
                { name: '' }
            ],
            loading: false,   // 
            searchValue: {},  // 
            dataList: [],     // 
            deptList: [],     // 
            roleList: [],     // 
            jobList: [],      // 
            userDetail: {},   // 
            total: 0,         // 
            pageSize: 10,     // Size
            pageNo: 1,        // 
            type: 0,          //  0:1:
            setUserModalShow: false, // 
            newPasswordModalShow: false, // 
            roleVisible: false,      // 
            deptModellShow: false,   // 
            jobModellShow: false,     // 
        }
    }
    componentDidMount() {
        this.queryTableList();
        this.queryDeptList();
        this.queryRoleList();
        this.queryJobList();
    }
    // 
    handleSearchBtn = (value) =>{
        this.setState({
            searchValue: {...value}
        },() => {
            this.queryTableList();
        })
    }
    // 
    queryTableList = (pageNo=1, pageSize=10) =>{
        this.setState({
            loading: true,
        },() => {
            const params = {
                ...this.state.searchValue,
                pageNo,
                pageSize,
            }
            this.apis.pageListUser({...params},(res) => {
                if(res.code === '200') {
                    if(pageNo > 1 && res.data.list.length === 0) {
                        this.queryTableList(res.data.pages,pageSize);
                    }else{
                        this.setState({
                            dataList: res.data.list,
                            total: res.data.total,
                            loading: false,
                            pageNo: res.data.pageNum,
                            pageSize: res.data.pageSize
                        })
                    }
                }
            })
        })
    }
    // 
    changeData = (data) => {
        console.log(data,'777777777777777')
        data.map(item => {
            if (item.list && item.list.length) {
                const val= item['list'];
                delete item['list'];
                item['children']=val;
                this.changeData(item.children);
            }
            return {...item}
        })
        return data
    }
    // 
    queryDeptList = () =>{
        this.apis.queryDeptList({},(res) => {
            if(res.code === '200') {
                const list = this.changeData(res.data);
                this.setState({
                    deptList: list,
                })
            }
        })
    }
    // 
    queryRoleList = () =>{
        this.apis.pageListRole({},(res) => {
            if(res.code === '200') {
                const list = this.changeData(res.data.list);
                this.setState({
                    roleList: list,
                })
            }
        })
    }
    // 
    queryJobList = () => {
        this.apis.getPageListJobs({},(res) => {
            if(res.code === '200') {
                this.setState({
                    jobList: res.data.list,
                })
            }
        })
    }
    // 
    handleChangePage = (pageNo,pageSize) => {
        this.queryTableList(pageNo, pageSize);
    }
    // 
    handleClickAdd = () => {
        this.setState({
            setUserModalShow: true,
            type: 0,
            userDetail: {}
        })
    }
    //  -- 
    handleCancel = () => {
        this.setState({
            setUserModalShow: false,
        },() => {
            this.refs.UserModal.resetFields();
        })
    }
    // 
    handleEdit = (item) => {
        this.setState({
            setUserModalShow: true,
            type: 1,
            userDetail: item
        })
    }
    //  -- 
    handleaddsetMeal = (values) => {
        const params = {
            ...values
        }
        delete params['type'];
        if (values.type === 0) { // 
            this.apis.addUser(params,(res) => {
                if(res.code === '200') {
                    message.success('');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }else{ // 
            params['bizId'] = this.state.userDetail.bizId;
            this.apis.updateUser(params,(res) => {
                if(res.code === '200') {
                    message.success('');
                    this.handleCancel();
                    this.queryTableList(this.state.pageNo,this.state.pageSize);
                }
            })
        }
    }
    //  
    handleDelete = (bizId) => {
        this.apis.deleteUser({bizId},(res) => {
            if(res.code === '200') {
                message.success('');
                this.queryTableList(this.state.pageNo,this.state.pageSize);
            }
        })
    }
    // 
    handleClickRole = (item) => {
        console.log(item, 'SSSSSSSSSSSSS')
        this.setState({
            roleVisible: true,
            userDetail: item
        })
    }
    // 
    handleRoleOk = (checkedList) => {
        const params = {
            userId: this.state.userDetail.bizId,
            roleIdList: checkedList
        }
        this.apis.addUserInRole(params,(res) => {
            if (res.code === '200') {
                message.success('');
                this.handleRoleCancle();
                this.queryTableList(this.state.pageNo,this.state.pageSize);
            }
        })
    }
    // 
    handleRoleCancle = () => {
        this.setState({
            roleVisible: false,
        })
    }
    // 
    handleClickPassword = (item) => {
        this.setState({
            newPasswordModalShow: true,
            userDetail: item
        })
    }
    // 
    handlePasswordCancel = () => {
        this.setState({
            newPasswordModalShow: false,
        },() => {
            this.refs.NewPassword.resetFields();
        })
    }
    // 
    handleaddsetPasswordMeal = (values) => {
        const params = {
            bizId: this.state.userDetail.bizId,
            passWord: values.passWord
        }
        this.apis.updateUser( params, (res) => {
            if(res.code === '200') {
                message.success('');
                this.handlePasswordCancel();
            }
        })
    }
    // 
    handleDeptModelShow = (item) => {
        this.setState({
            deptModellShow: true,
            userDetail: item
        })
    }
    // --
    handleDeptCancel = () => {
        this.setState({
            deptModellShow: false,
            userDetail: {}
        })
    }
    // --
    handleDeptSure = (deptList) => {
        const params = {
            userId: this.state.userDetail.bizId,
            deptIdList: deptList
        }
        this.apis.addUserInDept(params,(res) => {
            if (res.code === '200') {
                message.success('');
                this.handleDeptCancel();
                this.queryTableList(this.state.pageNo,this.state.pageSize);
            }
        })
    }
    //  -- 
    handleJobModelShow = (item) => {
        this.setState({
            jobModellShow: true,
            userDetail: item
        })
    }
    // --
    handleJobCancle = () => {
        this.setState({
            jobModellShow: false,
            userDetail: {}
        })
    }
    // --
    handleJobOk = (jobIdList) => {
        const params = {
            userId: this.state.userDetail.bizId,
            jobIdList: jobIdList
        }
        this.apis.addUserInJob(params,(res) => {
            if (res.code === '200') {
                message.success('');
                this.handleJobCancle();
                this.queryTableList(this.state.pageNo,this.state.pageSize);
            }
        })
    }
    render() {
        const { breadcrumbData, dataList, deptList, loading, setUserModalShow, 
            pageNo, pageSize, total, userDetail, roleList, type, newPasswordModalShow,
            roleVisible, deptModellShow, jobModellShow, jobList } = this.state;

        return (
            <div className="userManage">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {/*  */}
                    <div className="p-search">
                        <SearchForm deptList={deptList} handleSearchBtn={this.handleSearchBtn} />
                    </div>
                    <div className="addBtn">
                        <Button type="primary" onClick={this.handleClickAdd}></Button>
                    </div>
                    <div className="table-list">
                        <Table 
                            loading={loading} 
                            data={dataList} 
                            pageNo={pageNo}
                            pageSize={pageSize}
                            total={total}
                            handleChangePage={this.queryTableList}  // 
                            handleDelete={this.handleDelete} // 
                            handleEdit={this.handleEdit} // 
                            handleClickPassword={this.handleClickPassword} // 
                            handleClickRole={this.handleClickRole} // 
                            handleDept={this.handleDeptModelShow} // 
                            handleJob={this.handleJobModelShow} // 
                        />
                    </div>
                </div>
                {/*  */}
                <UserModal
                    ref="UserModal"
                    setUserModalShow={setUserModalShow}
                    userDetail={userDetail}
                    type={type}
                    deptList={deptList}
                    roleList={roleList}
                    handleaddsetMeal={this.handleaddsetMeal}
                    handleCancel={this.handleCancel}
                />
                {/*  */}
                <NewPassword
                    ref="NewPassword"
                    newPasswordModalShow={newPasswordModalShow}
                    userDetail={userDetail}
                    handleaddsetPasswordMeal={this.handleaddsetPasswordMeal}
                    handlePasswordCancel={this.handlePasswordCancel}
                />
                {/*  */}
                <RoleAuthorization
                    ref="RoleAuthorization"
                    visible={roleVisible}
                    data={roleList}
                    checkedList={userDetail.roleIdListStr}
                    handleCancel={this.handleRoleCancle}
                    handleOk={this.handleRoleOk}
                />
                {/*  */}
                <DeptModel
                    ref="DeptModel"
                    deptData={deptList}
                    checkedDeptCode={userDetail.deptIdListStr}
                    deptModellShow={deptModellShow}
                    handleCancel={this.handleDeptCancel} // 
                    handleaddsetMeal={this.handleDeptSure}
                />
                {/*  */}
                <JobAuthorization 
                    ref="DeptModel"
                    visible={jobModellShow}
                    data={jobList}
                    name={userDetail.dspName}
                    checkedList={userDetail.jobIdListStr}
                    handleCancel={this.handleJobCancle}
                    handleOk={this.handleJobOk}
                />
            </div>
        )
    }
}
export default userManage