import React, { Component } from 'react'
import { Form, Input, Modal, Select, Table, Tabs, Row, Col, message, Upload, Transfer, Button, Icon, Radio } from 'antd'
import './index.less'
import Api from '../../../api/index';
import BreadcrumbLink from '../../../components/bread'
import { searchToParams } from '../../../utils/utils.js'
const { Option } = Select;
const { TextArea } = Input;
const { TabPane } = Tabs

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            const { isEdit, record } = searchToParams();
            this.state = {
                fileList: [],
                pageSize: 10,
                page: 1,
                record,
                rightData: [],
                leftData: [],
                listUsersData: [],//
                selectedKeys: [],
                selectedKeysTwo: [],
                selectedKeysThree: [],
                listUsersOneData: [],//1
                listUsersTwoData: [],//2
                listUsersThreeData: [],//
                isEdit,
                listFileTypesData: [],//
                pageListUserData: [],///
                listQMSDeptData: [],///
                listAllApplyReasonsData: [],//
                listAllApplyTypesData: [],//
                // value: true,
                fileList: [],
                pageData: {
                    pageSize: 100,
                    page: 1
                },
                breadcrumbData: [
                    { name: '' },
                    { name: '' },
                    { name: 'ppt' }
                ],
            }
        }
        componentDidMount() {
            this.listUsers()
            this.listFileTypes()//
            this.pageListUser()///
            this.listQMSDept()///
            this.listAllApplyReasons()//
            this.listAllApplyTypes()//
        }

        callback = (key) => {

        }

        //
        listFileTypes = () => {
            Api.listFileTypes({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listFileTypesData: res.data.list,
                    })
                }
            })
        }
        ///
        pageListUser = () => {
            const { pageData } = this.state
            Api.pageListUser({
                pageSize: pageData.pageSize,
                pageNo: pageData.page
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        pageListUserData: res.data.list,
                    })
                }
            })
        }
        ///
        listQMSDept = () => {
            Api.listQMSDept({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listQMSDeptData: res.data,
                    })
                }
            })
        }
        //
        listAllApplyReasons = () => {
            Api.listAllApplyReasons({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listAllApplyReasonsData: res.data.list,
                    })
                }
            })
        }
        //
        listAllApplyTypes = () => {
            Api.listAllApplyTypes({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listAllApplyTypesData: res.data.list,
                    })
                }
            })
        }
        //1
        handleOneChange = (nextTargetKeys, direction, moveKeys, ReactElement) => {
            this.setState({
                listUsersOneData: nextTargetKeys
            })
        };
        //2
        handleTwoChange = (nextTargetKeys, direction, moveKeys) => {
            this.setState({
                listUsersTwoData: nextTargetKeys
            })
        };
        //
        handleThreeChange = (nextTargetKeys, direction, moveKeys) => {
            this.setState({
                listUsersThreeData: nextTargetKeys
            })
        };
        handleSelectChange = (sourceSelectedKeys, targetSelectedKeys, ReactElement) => {
            this.setState({
                selectedKeys: [...sourceSelectedKeys, ...targetSelectedKeys]
            });
        };
        handleSelectChangeTwo = (sourceSelectedKeys, targetSelectedKeys) => {
            this.setState({
                selectedKeysTwo: [...sourceSelectedKeys, ...targetSelectedKeys]
            });
        };
        handleSelectChangeThree = (sourceSelectedKeys, targetSelectedKeys) => {
            this.setState({
                selectedKeysThree: [...sourceSelectedKeys, ...targetSelectedKeys]
            });
        };
        //
        handleSendBack = () => {
            this.props.history.push('/fileManagement/feedBack')
        }

        //
        handleUpData = () => {
            const { listUsersOneData, listUsersTwoData, listUsersThreeData } = this.state
            this.props.form.validateFieldsAndScroll((err, values) => {
                const officePageSieData = parseInt(values.officePageSie)
                if (true) {
                    Api.addFile({
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                        officePageSie: officePageSieData,
                        fileTypeId: values.fileTypeId,
                        fileVersion: values.fileVersion == undefined ? values.fileVersion : values.fileVersion.trim(),
                        fileInfo: values.fileInfo == undefined ? values.fileInfo : values.fileInfo.trim(),
                        applyDeptId: values.applyDeptId,
                        applyType: values.applyType,
                        applyReasonId: values.applyReasonId,
                        approverUserId: values.approverUserId,
                        auditorId: values.auditorId,
                        yearAuditCycle: values.yearAuditCycle,
                        signator1: listUsersOneData,
                        signator2: listUsersTwoData,
                        approverList: listUsersThreeData,
                        annualVerificationDeptId: values.annualVerificationDeptId,
                        isAnnualVerification: values.isAnnualVerification,
                        submitType: 4,
                    }, (res) => {
                        if (res.code === '200') {
                            message.success(res.message)
                            this.props.history.push('/fileManagement/feedBack')
                        } else {
                            message.warning(res.message)
                        }
                    })
                }
            });
        }

        //
        handleSave = () => {
            const { listUsersOneData, listUsersTwoData, listUsersThreeData } = this.state
            this.props.form.validateFieldsAndScroll((err, values) => {
                const officePageSieData = parseInt(values.officePageSie)
                if (true) {
                    Api.addFile({
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                        officePageSie: officePageSieData,
                        fileTypeId: values.fileTypeId,
                        fileVersion: values.fileVersion == undefined ? values.fileVersion : values.fileVersion.trim(),
                        fileInfo: values.fileInfo == undefined ? values.fileInfo : values.fileInfo.trim(),
                        applyDeptId: values.applyDeptId,
                        applyType: values.applyType,
                        applyReasonId: values.applyReasonId,
                        approverUserId: values.approverUserId,
                        auditorId: values.auditorId,
                        yearAuditCycle: values.yearAuditCycle,
                        signator1: listUsersOneData,
                        signator2: listUsersTwoData,
                        approverList: listUsersThreeData,
                        annualVerificationDeptId: values.annualVerificationDeptId,
                        isAnnualVerification: values.isAnnualVerification,
                        submitType: 1,
                    }, (res) => {
                        if (res.code === '200') {
                            message.success(res.message)
                            this.props.history.push('/fileManagement/feedBack')
                        } else {
                            message.warning(res.message)
                        }
                    })
                }
            });
        }

        normFile = (e) => {
            if (Array.isArray(e)) {
                return e;
            }
            let fileList = e.fileList;
            fileList = fileList.slice(-1);
            return e && fileList;
        }
        beforeUpload = (file) => {
            const isLt = file.size / 1024 / 1024 < 100;
            if (!isLt) {
                message.error('');
            }
            return isLt;
        }
        // 
        handleChange = (info) => {
            //fileList
            let { fileList } = info;

            const status = info.file.status;
            if (status) {
                if (info.file.status !== 'uploading') {
                }
                if (status === 'done') {
                    fileList = fileList.slice(-1);
                } else if (status === 'error') {
                    message.error(`${info.file.name} `);
                }
                //state
                this.setState({ fileList });
            }
        }
        // 
        listUsers = () => {
            const { pageSize, page } = this.state
            const data = {
                pageSize,
                pageNo: page
            }
            Api.listUsers(data, (res) => {
                if (res.code === '200') {
                    let _obj = {};
                    _obj['listUsersData'] = res.data.list.map(v => {
                        return {
                            key: v.userId,
                            title: v.userName
                        }
                    });
                    this.setState(_obj)
                }
            })
        }
        filterOption = (inputValue, option) => option.title && option.title.indexOf(inputValue) > -1;

        //
        onChangeYears = (e) => {
            this.setState({
                valueYears: e.target.value,
            });
        };
        //
        handleYearChange = (value) => {
        }
        onBlur() {
        }

        onFocus() {
        }
        render() {
            const {
                form
            } = this.props;
            const {
                leftData,
                rightData,
                selectedKeys,
                breadcrumbData,
                record,
                isEdit,
                fileList,
                listUsersOneData,
                listUsersTwoData,
                listUsersThreeData,
                selectedKeysThree,
                selectedKeysTwo,
                listUsersData,
                valueYears,
                listFileTypesData,
                pageListUserData,
                listAllApplyReasonsData,
                listAllApplyTypesData,
                listQMSDeptData
            } = this.state;

            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 14 },
            };
            const uploadProps = {
                action: `/file/uploadFiles`,
                onChange: this.handleChange,
                multiple: false,
                beforeUpload: this.beforeUpload,
            };

            //
            const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
                return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
            }) : null
            ///
            const pageListUser = pageListUserData ? pageListUserData.map(res => {
                return (<Option key={res.bizId} values={res.bizId}>{res.dspName}</Option>)
            }) : null
            ///
            const listQMSDept = listQMSDeptData ? listQMSDeptData.map(res => {
                return (<Option key={res.bizId} values={res.bizId}>{res.deptName}</Option>)
            }) : null
            //
            const listAllApplyReasons = listAllApplyReasonsData ? listAllApplyReasonsData.map(res => {
                return (<Option key={res.applyReasonId} values={res.applyReasonId}>{res.applyReasonName}</Option>)
            }) : null
            //
            const listAllApplyTypes = listAllApplyTypesData ? listAllApplyTypesData.map(res => {
                return (<Option key={res.applyTypeId} values={res.applyTypeId}>{res.applyTypeName}</Option>)
            }) : null
            return (
                <div className="wordForm">
                    <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                    {/* p-content-wrapper */}
                    <div className='p-content-wrapper'>
                        <Form>
                            <Tabs defaultActiveKey="1" onChange={this.callback}>
                                <TabPane tab="" key="1">
                                    <Form>
                                        <Row>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('fileNo', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Input placeholder="" autoComplete='off' />
                                                    )}
                                                </Form.Item>
                                            </Col>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('fileName', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Input placeholder="" autoComplete='off' />
                                                    )}
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('officePageSie', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Input placeholder="" autoComplete='off' />
                                                    )}
                                                </Form.Item>
                                            </Col>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('fileTypeId', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Select
                                                            allowClear
                                                            placeholder=""
                                                            onChange={this.queryEleList}
                                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                                        >
                                                            {listFileTypes}
                                                        </Select>
                                                    )}
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('fileVersion', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Input placeholder="" autoComplete='off' />
                                                    )}
                                                </Form.Item>
                                            </Col>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('fileInfo', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <TextArea placeholder="" autoComplete='off' />
                                                    )}
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('applyDeptId', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Select
                                                            allowClear
                                                            placeholder=""
                                                            onChange={this.queryEleList}
                                                            onFocus={this.onFocus}
                                                            onBlur={this.onBlur}
                                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                                        >
                                                            {listQMSDept}
                                                        </Select>
                                                    )}
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('applyTypeId', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Select
                                                            allowClear
                                                            placeholder=""
                                                            onChange={this.queryEleList}
                                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                                        >
                                                            {listAllApplyTypes}
                                                        </Select>
                                                    )}
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('applyReasonId', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Select
                                                            allowClear
                                                            placeholder=""
                                                            onChange={this.queryEleList}
                                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                                        >
                                                            {listAllApplyReasons}
                                                        </Select>
                                                    )}
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('approverUserId', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Select
                                                            allowClear
                                                            placeholder=""
                                                            onChange={this.queryEleList}
                                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                                        >
                                                            {pageListUser}
                                                        </Select>
                                                    )}
                                                </Form.Item>
                                            </Col>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('auditorId', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Select
                                                            allowClear
                                                            placeholder=""
                                                            onChange={this.queryEleList}
                                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                                        >
                                                            {pageListUser}
                                                        </Select>
                                                    )}
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row>
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('isAnnualVerification', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Radio.Group onChange={this.onChangeYears} value={this.state.value}>
                                                            <Radio value={true}></Radio>
                                                            <Radio value={false}></Radio>
                                                        </Radio.Group>
                                                    )}
                                                </Form.Item>
                                            </Col>
                                            {
                                                valueYears === true ?
                                                    <Col span={8}>
                                                        <Form.Item
                                                            {...formItemLayout}
                                                            label=""
                                                        >
                                                            {getFieldDecorator('yearAuditCycle', {
                                                                initialValue: isEdit === "true" ? record.fileNo : null
                                                            })(
                                                                <Select
                                                                    style={{ width: 200 }}
                                                                    placeholder="Select a person"
                                                                    optionFilterProp="children"
                                                                    onChange={this.handleYearChange}
                                                                    onFocus={this.onFocus}
                                                                    onBlur={this.onBlur}
                                                                    filterOption={(input, option) =>
                                                                        option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                                                    }
                                                                >
                                                                    <Option value={1}></Option>
                                                                    <Option value={2}></Option>
                                                                </Select>
                                                            )}
                                                        </Form.Item>
                                                    </Col>
                                                    :
                                                    null
                                            }
                                            <Col span={8}>
                                                <Form.Item
                                                    {...formItemLayout}
                                                    label=""
                                                >
                                                    {getFieldDecorator('annualVerificationDeptId', {
                                                        initialValue: isEdit === "true" ? record.fileNo : null
                                                    })(
                                                        <Select
                                                            allowClear
                                                            placeholder=""
                                                            onChange={this.queryEleList}
                                                            getPopupContainer={triggerNode => triggerNode.parentNode}
                                                        >
                                                            {listQMSDept}
                                                        </Select>
                                                    )}
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                    </Form>
                                </TabPane>
                                <TabPane tab="" key="2">
                                    <div className="addSkip">
                                        <Upload {...uploadProps} fileList={this.state.fileList}>
                                            <Button>
                                                <Icon type="plus" /> 
                                            </Button>
                                        </Upload>
                                    </div>
                                </TabPane>
                                <TabPane tab="" key="3">
                                    <div style={{ paddingLeft: 33 }}>
                                        <div>1</div>
                                        <Transfer
                                            // showSearch
                                            // onSearch={this.handleSearch}
                                            ref={(transfer) => { this.transfer = transfer }}
                                            dataSource={listUsersData}
                                            titles={[``, ``]}
                                            targetKeys={listUsersOneData}
                                            selectedKeys={selectedKeys}
                                            onChange={this.handleOneChange}
                                            onSelectChange={this.handleSelectChange}
                                            render={item => item.title}
                                            // disabled={disabled}
                                            listStyle={{
                                                width: 270,
                                                height: 320,
                                            }}
                                            loading={true}
                                            filterOption={this.filterOption}
                                        />

                                        <div>2</div>
                                        <Transfer
                                            ref={(transfer) => { this.transfer = transfer }}
                                            dataSource={listUsersData}
                                            titles={[``, ``]}
                                            targetKeys={listUsersTwoData}
                                            selectedKeys={selectedKeysTwo}
                                            onChange={this.handleTwoChange}
                                            onSelectChange={this.handleSelectChangeTwo}
                                            render={item => item.title}
                                            // disabled={disabled}
                                            listStyle={{
                                                width: 270,
                                                height: 320,
                                            }}
                                            loading={true}
                                            showSearch
                                            filterOption={this.filterOption}
                                        />

                                        <div></div>
                                        <Transfer
                                            ref={(transfer) => { this.transfer = transfer }}
                                            dataSource={listUsersData}
                                            titles={[``, ``]}
                                            targetKeys={listUsersThreeData}
                                            selectedKeys={selectedKeysThree}
                                            onChange={this.handleThreeChange}
                                            onSelectChange={this.handleSelectChangeThree}
                                            render={item => item.title}
                                            // disabled={disabled}
                                            listStyle={{
                                                width: 270,
                                                height: 320,
                                            }}
                                            loading={true}
                                            showSearch
                                            filterOption={this.filterOption}
                                        />

                                    </div>
                                </TabPane>
                            </Tabs>
                            <div style={{ position: "absolute", zIndex: 1, bottom: 0, }}>
                                <Button onClick={this.handleUpData} type="primary"></Button>
                                <Button onClick={this.handleSave}></Button>
                                <Button onClick={this.handleSendBack}></Button>
                            </div>
                        </Form>
                    </div>
                </div>
            );
        }
    }
);
export default App