import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Tabs, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
import ProcessFromWrap from './processForm';
import WordFromWrap from './wordForm';
import ExcelFromWrap from './excelForm';
// import EditFromWrap from './editForm';
const Option = Select
const { TabPane } = Tabs
const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            checkColumns: [],
            pageSize: 10,
            page: 1,
            isEdit: true,
            listFileTypesData: [],//
            listApplyDeptInFileModelData: [],//
            writtenData: [],//
            showSizeChanger: true,//
            listAllApplyTypesData: [],//
            processVisible: false,// 
            wordVisible: false,//Word 
            excelVisible: false,//excel 
            editVisible: false,//  
            // },
            selectTime: [],
            breadcrumbData: [
                { name: '' },
                { name: '' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '',
                key: 'fileNo',
                width: '200px',
                render: (text, record, index) => {
                    if (record.fileNo) {
                        return (
                            <div className='ellipsis'>{record.fileNo}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '',
                key: 'fileName',
                render: (text, record, index) => {
                    if (record.fileName) {
                        return (
                            <div className='ellipsis'>{record.fileName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '',
                key: 'fujian',
                dataIndex: 'fujian',
                width: 200,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.handleFujian}></a> &nbsp;
                        </div>
                    )
                }
            }, {
                title: '/',
                key: 'fileVersion',
                render: (text, record, index) => {
                    if (record.fileVersion) {
                        return (
                            <div className='ellipsis'>{record.fileVersion}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '',
                key: 'applyDeptName',
                render: (text, record, index) => {
                    if (record.applyDeptName) {
                        return (
                            <div className='ellipsis'>{record.applyDeptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '',
                key: 'applyUserName',
                render: (text, record, index) => {
                    if (record.applyUserName) {
                        return (
                            <div className='ellipsis'>{record.applyUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '',
                key: 'applyDate',
                render: (text, record, index) => {
                    if (record.applyDate) {
                        return (
                            <div className='ellipsis'>{record.applyDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '',
                key: 'fileStatus',
                render: (text, record, index) => {
                    if (record.fileStatus === 1) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } if (record.fileStatus === 2) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } if (record.fileStatus === 3) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } if (record.fileStatus === 4) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } if (record.fileStatus === 5) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } if (record.fileStatus === 6) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } if (record.fileStatus === 7) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '',
                key: 'applyType',
                render: (text, record, index) => {
                    if (record.applyType === 1) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } if (record.applyType === 2) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } if (record.applyType === 3) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } if (record.applyType === 4) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } if (record.applyType === 5) {
                        return (
                            <div className='ellipsis'></div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '',
                key: 'operation',
                dataIndex: 'itemStatus',
                fixed: 'right',
                width: 200,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.handleEdit.bind(this, record)}></a> &nbsp;
                            <a onClick={this.feedDelete.bind(this, record)}></a> &nbsp;
                            <a onClick={this.processRecording.bind(this, record)}></a>
                        </div>
                    )
                }
            }]
        }, () => {
            this.pageListFile()
            this.listFileTypes()//
            this.listUsers()//
            this.listApplyDeptInFileModel()//
            this.listAllApplyTypes()//
        }
        )
    }
    //  
    pageListFile() {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page
        }
        Api.pageListFile(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list,
                    total: res.data.total,
                },
                    message.success(res.message)
                )
            } else {
                message.warning(res.message)
            }
        })
    }
    //
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                if (true) {
                    const { pageSize, page } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    Api.pageListFile({
                        pageSize,
                        pageNo: page,
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                        fileTypeId: values.fileTypeId,
                        applyDeptId: values.applyDeptId,
                        userId: values.userId,
                        applyTypeId: values.applyTypeId,
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                total: res.data.total,
                                planData: res.data.list
                            })
                        }
                    })
                }
            });
        })
    }
    //
    handleTableChange = (page) => {
        this.setState({
            page,
        }, () => {
            this.handleSearchPeople()
        })
    }
    // 
    handleReset = () => {
        this.props.form.resetFields();
        this.pageListFile()
    }
    //
    listFileTypes = () => {
        Api.listFileTypes({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listFileTypesData: res.data.list,
                })
            }
        })
    }
    //
    listApplyDeptInFileModel = () => {
        Api.listApplyDeptInFileModel({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listApplyDeptInFileModelData: res.data.list
                })
            }
        })
    }
    //
    listUsers = () => {
        Api.listUsers({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    writtenData: res.data.list,
                })
            }
        })
    }
    //
    listAllApplyTypes = () => {
        Api.listAllApplyTypes({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listAllApplyTypesData: res.data.list,
                })
            }
        })
    }

    //
    feedDelete = (record) => {
        Api.deleteFileInFileQuery({
            bizId: record.bizId
        }, (res) => {
            if (res.code === "200") {
                message.success("")
                this.pageListFile()
            } else {
                message.warning(res.message)
            }
        })

    }
    // 
    processRecording = (record) => {

        Api.pageListLogInfo({
            fileId: record.bizId
        }, (res) => {
            if (res.code === '200') {
                this.setState({
                    pageListLogInfoData: res.data.list
                })
            }
        })
        this.setState({
            processVisible: true,
        })
    }
    // 
    processCan = () => {
        this.setState({
            processVisible: false,
        })
    }
    // 
    processNo = () => {
        this.setState({
            processVisible: false,
        })
    }
    //Word 
    wordNo = () => {
        this.setState({
            wordVisible: false,
        })
    }
    // excel
    addExcel = () => {
        this.props.history.push('/fileManagement/feedBack/excelFrom')
    }
    // ppt
    addPPT = () => {
        this.props.history.push('/fileManagement/feedBack/addPptForm')
    }
    // 
    handleEdit = (record) => {
        this.props.history.push(`/fileManagement/feedBack/wordFormTwo?bizId=${record.bizId}`)
    }
    //WORD
    addWord = () => {
        this.props.history.push('/fileManagement/feedBack/wordForm')
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 8 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            processVisible,
            wordVisible,
            excelVisible,
            editVisible,
            listFileTypesData,
            listApplyDeptInFileModelData,
            writtenData,
            listAllApplyTypesData,
            pageListLogInfoData,
            showSizeChanger,
            total
        } = this.state;

        //
        const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
            return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
        }) : null
        //
        const listApplyDeptInFileModel = listApplyDeptInFileModelData ? listApplyDeptInFileModelData.map(res => {
            return (<Option key={res.applyDeptId} values={res.applyDeptId}>{res.applyDeptName}</Option>)
        }) : null
        //
        const writtenBy = writtenData ? writtenData.map(res => {
            return (<Option key={res.userId} values={res.userId}>{res.userName}</Option>)
        }) : null
        //
        const listAllApplyTypes = listAllApplyTypesData ? listAllApplyTypesData.map(res => {
            return (<Option key={res.applyTypeId} values={res.applyTypeId}>{res.applyTypeName}</Option>)
        }) : null

        const pagination = total > 10 ? {
            total: this.state.total,
            pageSize: this.state.pageSize,
            current: this.state.page,
            onChange: this.handleTableChange,
            showQuickJumper: true,
            showSizeChanger,
            onShowSizeChange: this.onShowSizeChange,
        } : false
        return (
            <div className="feedBcak">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label=""
                                    >
                                        {getFieldDecorator('fileNo', {
                                        })(
                                            <Input placeholder="" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label=""
                                    >
                                        {getFieldDecorator('fileName', {
                                        })(
                                            <Input placeholder="" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label=""
                                    >
                                        {getFieldDecorator('fileTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder=""
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listFileTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label=""
                                    >
                                        {getFieldDecorator('applyDeptId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder=""
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listApplyDeptInFileModel}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label=""
                                    >
                                        {getFieldDecorator('userId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder=""
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {writtenBy}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label=""
                                    >
                                        {getFieldDecorator('applyTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder=""
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listAllApplyTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Button onClick={this.handleReset} style={{ float: 'right' }}></Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}></Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <div>
                        <Button style={{ marginLeft: '20px', background: '#4897FF', color: '#FFFFFF' }} onClick={this.addWord}>WORD</Button> &nbsp;
                        <Button style={{ border: '1px solid #4897FF', color: '#4897FF' }} onClick={this.addExcel}>Excel</Button> &nbsp;
                        <Button style={{ border: '1px solid #4897FF', color: '#4897FF' }} onClick={this.addPPT}>PPT</Button>
                    </div>
                    <div style={{ marginTop: "30px" }}>
                        <Table
                            style={{ marginTop: '-12px', marginLeft: '20px' }}
                            columns={checkColumns}
                            dataSource={planData}
                            pagination={pagination}
                            key="bizId"
                        ></Table>
                    </div>
                    {/*  */}
                    <ProcessFromWrap
                        visible={processVisible}
                        onCancel={this.processCan}
                        onCreate={this.processNo}
                        pageListLogInfoData={pageListLogInfoData}
                    />
                    {/*  */}
                    {/* <EditFromWrap
                        visible={editVisible}
                        onCancel={this.editCan}
                        onCreate={this.editNo}
                    /> */}
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner