import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Card, Select, Statistic } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../api/index'
import BreadcrumbLink from '../../components/bread'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import axios from 'axios'
import { searchToParams } from '../../utils/utils.js'
const FormItem = Form.Item
const { Option } = Select;
const Search = Input.Search;
const { RangePicker } = DatePicker;
const { Countdown } = Statistic
class onBanner extends Component {
    constructor(props) {
        super(props)
        const { bizId, isLook, isTextLookTwo, isTestTwo, isTextLook, examUserPlanModelId } = searchToParams();
        this.state = {
            msg: "",
            isLook,
            isTextLook,
            isTextLookTwo,
            bizId, // 
            examUserPlanModelId,
            handleLookData: [],// data
            handleStartExamData: [],//  data
            lookData: [],
            breadcrumbData: [
                { name: '' },
                { name: '' },
                { name: '' }
            ],
        }
    }
    componentDidMount() {
        this.handleStartExam()///
    }
    //
    onFinish() {
        this.handleConfirmation()
    }
    // 
    handleStartExam = () => {
        const { examUserPlanModelId, isTextLookTwo } = this.state

        isTextLookTwo ? //  
            Api.lookExam({
                examUserPlanModelId: examUserPlanModelId ? examUserPlanModelId : []
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        handleLookData: res.data
                    })
                }
            })
            :
            Api.startExam({
                examUserPlanModelId: examUserPlanModelId ? examUserPlanModelId : []
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        handleStartExamData: res.data
                    })
                }
            })
    }
    //
    handleLoginOut = () => {
        this.props.history.push(`/testManagement/testJoin`)
    }
    // 
    handleConfirmation = () => {
        const { handleStartExamData } = this.state
        console.log(handleStartExamData, '555555555555 handleStartExamData')
        this.props.form.validateFieldsAndScroll((errors, values) => {
            console.log(values, '2222222222 values')
            // const questionsTwo = handleStartExamData ? handleStartExamData.examQuestionTypes.map((val, i) => { //examQuestionTypes
            //     return val
            // }) : [] 
            // const questions = questionsTwo ? questionsTwo.map((val, i) => {
            //     return (
            //         val.questionTypeName === "" ?
            //             val.questionsPoints
            //             :
            //             val.questionTypeName === "" ?
            //                 val.questionsPoints
            //                 :
            //                 null
            //     )
            // }) : []
            if (true) {
                const { examUserPlanModelId, handleStartExamData } = this.state
                Api.submitExamAnswer({
                    examUserPlanId: examUserPlanModelId,
                    examPlanId: handleStartExamData.examPlanId,
                    examId: handleStartExamData.examId
                }, (res) => {
                    if (res.code === "200") {
                        message.success(res.message)
                        this.props.history.push('testManagement/testJoin')
                    }
                })
            }
        })
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            isLook,
            isTextLook,
            breadcrumbData,
            handleStartExamData,
            handleLookData,
            isTextLookTwo
        } = this.state;

        const topic = handleStartExamData && handleStartExamData.examQuestionTypes ? handleStartExamData.examQuestionTypes.map((val, i) => {  //
            return (
                <div className="startExammTwo">
                    <div className="examTitle">
                        <div style={{ marginLeft: '5px' }}>
                            <span>{val.questionTypeName} ({val.questionsPoints} /)</span>
                        </div>
                    </div>
                    {
                        val.questionTypeName === "" ?
                            <span>
                                {val.questionForPreview.map((values, index) => {
                                    return <Card title={<div> {Number(index + 1)}{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                                        <p>A:{values.questionA}</p>
                                        <p>B:{values.questionB}</p>
                                        <p>C:{values.questionC}</p>
                                        <p>D:{values.questionD}</p>
                                        <p>E:{values.questionE}</p>
                                        <p>F:{values.questionF}</p>
                                        <p>
                                            <FormItem
                                                {...formItemLayout}
                                                label=":"
                                            >
                                                {
                                                    getFieldDecorator([values.bizId], {
                                                    })(
                                                        <Select onChange={this.handleSelect} style={{ width: '100px' }} placeholder="">
                                                            <Option value="A">A</Option>
                                                            <Option value="B">B</Option>
                                                            <Option value="C">C</Option>
                                                            <Option value="D">D</Option>
                                                            <Option value="E">E</Option>
                                                            <Option value="F">F</Option>
                                                        </Select>
                                                    )
                                                }
                                            </FormItem>
                                        </p>
                                    </Card>
                                })}
                            </span>
                            :

                            val.questionTypeName === "" ?
                                <span>
                                    {val.questionForPreview.map((values, index) => {
                                        return <Card title={<div> {Number(index + 1)}{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                                            <p>A:{values.questionA}</p>
                                            <p>B:{values.questionB}</p>
                                            <p>C:{values.questionC}</p>
                                            <p>D:{values.questionD}</p>
                                            <p>E:{values.questionE}</p>
                                            <p>F:{values.questionF}</p>
                                            <p>
                                                <FormItem
                                                    {...formItemLayout}
                                                    label=":"
                                                >
                                                    {
                                                        getFieldDecorator([values.bizId])(
                                                            <Select onChange={this.handleStartExamData} mode="multiple" style={{ width: '100px' }} placeholder="">
                                                                <Option value="A">A</Option>
                                                                <Option value="B">B</Option>
                                                                <Option value="C">C</Option>
                                                                <Option value="D">D</Option>
                                                                <Option value="E">E</Option>
                                                                <Option value="F">F</Option>
                                                            </Select>
                                                        )
                                                    }
                                                </FormItem>
                                            </p>
                                        </Card>
                                    })}
                                </span>
                                :
                                null

                    }

                </div>
            )
        }) : ''

        const look = handleLookData && handleLookData.examQuestionTypes ? handleLookData.examQuestionTypes.map((val, i) => {    // 
            return (
                <div>
                    <div className="examTitle">
                        <div style={{ marginLeft: '5px' }}>
                            <span>{val.questionTypeName} (5/)</span>
                        </div>
                    </div>

                    {
                        val.questionTypeName === "" ?
                            <span>
                                {val.questionForPreview.map((values, index) => {
                                    return <Card title={<div> {Number(index + 1)}{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                                        <p>A:{values.questionA}</p>
                                        <p>B:{values.questionB}</p>
                                        <p>C:{values.questionC}</p>
                                        <p>D:{values.questionD}</p>
                                        <p>E:{values.questionE}</p>
                                        <p>F:{values.questionF}</p>
                                        <p>{values.userAnswer}  {values.userPoints} </p>
                                    </Card>
                                })}
                            </span>
                            :
                            val.questionTypeName === "" ?
                                <span>
                                    {val.questionForPreview.map((values, index) => {
                                        return <Card title={<div> {Number(index + 1)}{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                                            <p>A:{values.questionA}</p>
                                            <p>B:{values.questionB}</p>
                                            <p>C:{values.questionC}</p>
                                            <p>D:{values.questionD}</p>
                                            <p>E:{values.questionE}</p>
                                            <p>F:{values.questionF}</p>
                                            <p>{values.userAnswer} {values.userPoints} </p>
                                        </Card>
                                    })}
                                </span>
                                :
                                null

                    }

                </div>
            )
        }) : ''

        const dataTime = this.state.handleStartExamData && this.state.handleStartExamData.examDuration ? this.state.handleStartExamData.examDuration : [] // 
        const deadline = Date.now() + 1000 * 60 * dataTime; //  value
        return (
            <div className="startTheExam">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="information">
                        <div style={{ float: "right" }}>
                            {
                                dataTime > 60 ?
                                    <Countdown onFinish={this.onFinish} title="" value={deadline} format="H  m  s " />
                                    :
                                    dataTime > 1440 ?
                                        < Countdown onFinish={this.onFinish} title="" value={deadline} format="D  H  m  s " />
                                        :
                                        < Countdown onFinish={this.onFinish} title="" value={deadline} format=" m  s " />
                            }
                        </div>
                        <div>
                            <span></span><span>{handleStartExamData.dspName}</span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span></span><span>{handleStartExamData.examName}</span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span>{handleStartExamData.examDuration} </span>
                            <span style={{ marginLeft: '20px' }}>  {handleStartExamData.totalPoints} </span>
                            <span style={{ marginLeft: '20px' }}> {handleStartExamData.passPoints}</span>
                        </div>
                    </div>
                    {
                        isTextLook ?
                            topic
                            :
                            isTextLookTwo ?
                                look
                                :
                                ''
                    }
                </div>
                <div className="bottom-btn">
                    <Button style={{ float: 'right' }} onClick={this.handleLoginOut}></Button>
                    {
                        isTextLook ?
                            <Button style={{ float: 'right', marginRight: '20px' }} type="primary" onClick={this.handleConfirmation}></Button>
                            :
                            null
                    }
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner