/**
 * [searchToParams search]
 * @return {[JSON]}        []
 */
export const searchToParams = (type) => {
  const data = {}
  const {search} = window.location
  let realSearch = search.substring(1,search.length)
  if(type === 'login'){
    realSearch = realSearch.replace(/\|\|/g,'&')
  }
  realSearch.split('&').map(res=>{
    const resArray = res.split('=')
    let urlParams = ''
    resArray.map((dt,inx)=>{
      if(inx > 0){
        if(urlParams){
          urlParams += `=${dt}`
        }else{
          urlParams += `${dt}`
        }
      }
      return dt
    })
    data[resArray[0]]= urlParams ? decodeURI(urlParams) : ''
    return res
  })
  return data
}
/**
 * window.search
 * @param {String} name 
 */
export const getQueryStringByName = (name) => {
    const reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    const r = window.location.search.substr(1).match(reg);
    if (r != null) {
        return (r[2])
    };
    return null;
};
/**
 * [addZero 0]
 * @param {[string]} str []
 */
export const addZero = (str) => {
  const newStr = str + ''
  if(newStr.length > 1){
    return newStr
  }else{
    return '0' + newStr
  }
}
/**
 * 
 * * @param {date} date 
 * * @param {string: all date time} type 
 */
export const dataFormat = (date,type = 'all') => {
  let string = ''
  switch(type){
    case 'all':
      string = `${date.getFullYear()}-${addZero(date.getMonth()+1)}-${addZero(date.getDate())} ${addZero(date.getHours())}:${addZero(date.getMinutes())}:${addZero(date.getSeconds())}`;
      break;
    case 'date':
      string = `${date.getFullYear()}-${addZero(date.getMonth()+1)}-${addZero(date.getDate())}`
      break;
    case 'time':
      string = `${addZero(date.getHours())}:${addZero(date.getMinutes())}:${addZero(date.getSeconds())}`
      break;
    default:
      break;
  }
  return string
}
/**
 * [setCookie cookie]
 * @param {[string]} name  [cookie]
 * @param {[string]} value [cookie]
 * @param {[number]} day   []
 */
export const setCookie = (name, value, day) => {
   if(day !== 0){     //0expirescookie
       var expires = day * 24 * 60 * 60 * 1000;
       var date = new Date(+new Date()+expires);
       document.cookie = name + "=" + escape(value) + ";expires=" + date.toUTCString();
   }else{
     document.cookie = name + "=" + escape(value);
  }
};
/**
 * [uncode ]
 * @param  {[string]} str []
 * @return {[type]}        [description]
 */
export const uncode = (str='',type = 'default') => {
  str = str || ''
  if(type === 'default'){
    return str.replace(/&lt;/g,'<').replace(/&gt;/g,'>').replace(/&quot;/g,'"').replace(/&#39;/g,"'")
  }else{
    let newStr = str.replace(/&lt;script&gt;/g,'&script&').replace(/&lt;\/script&gt;/g,'&/script&').replace(/&lt;/g,'<').replace(/&gt;/g,'>').replace(/&quot;/g,'"').replace(/&#39;/g,"'")
    return newStr.replace(/&script&/g,'&lt;script&gt;').replace(/&\/script&/,'&lt;/script&gt;')
  }
}
export const getdate = (code) => {
      var time = new Date(code)
        , year = time.getFullYear()
        , month = time.getMonth() + 1
        , date = time.getDate()
        , hour = time.getHours() < 10 ? '0' + time.getHours() : time.getHours()
        , min = time.getMinutes() < 10 ? '0' + time.getMinutes() : time.getMinutes();
          return year + '-' + month + '-' + date + ' ' + hour + ':' + min;
    }
