import React, { Component } from 'react'
import './index.less'
import { Modal, Input, Form, Row, Col, Checkbox,Select, Transfer } from 'antd'


const FormItem = Form.Item;
const Option = Select.Option;

class categoryModal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            rightData: [],
            leftData: [],
            selectedKeys: [],
        }
    }
    handleOk = (e) => {
        e.preventDefault();
        const { form, type } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                values.type = type;
                values['category'] = {
                    '611919992795041792': '',
                    '611920443401703424': ''
                }[values.parentId]
                console.log(values)
                this.props.handleaddsetMeal(values)
            }
        })
    }
    // 
    handleCancel = () => {
        this.props.handleCancel();
    }
    render() {
        const { type, showModal, QMDetail } = this.props;
        const { getFieldDecorator } = this.props.form;
        return (
            <Modal
                className="categoryModal"
                title={type === 0 ? '-' : '-'}
                visible={showModal}
                width={520}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
            >
                <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                    <FormItem label="">
                        {getFieldDecorator('code', {
                            initialValue: QMDetail.code,
                            rules: [
                                { required: true, message: '' },
                            ],
                        })(
                            <Input placeholder="" />
                        )}
                    </FormItem>
                    <FormItem label="">
                        {getFieldDecorator('fullName', {
                            initialValue: QMDetail.fullName,
                            rules: [
                                { required: true, message: '' },
                            ],
                        })(
                            <Input placeholder="" />
                        )}
                    </FormItem>
                    <FormItem label="">
                        {getFieldDecorator('parentId', {
                            initialValue: QMDetail.parentId,
                            rules: [
                                { required: true, message: '' },
                            ],
                        })(
                            <Select placeholder="">
                                <Option value='611919992795041792'></Option>
                                <Option value='611920443401703424'></Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem label="" style={{marginBottom: 0}}>
                        <Row gutter={24}>
                            <Col span={6}>
                                <FormItem>
                                    {getFieldDecorator('enabled', {
                                        valuePropName: 'checked',
                                        initialValue: QMDetail['enabled'] ? true : false
                                    })(
                                        <Checkbox></Checkbox>
                                    )}
                                </FormItem>
                            </Col>
                            <Col span={9}>
                                <FormItem>
                                    {getFieldDecorator('allowEdit', {
                                        valuePropName: 'checked',
                                        initialValue: QMDetail['allowEdit'] ? true : false
                                    })(
                                        <Checkbox></Checkbox>
                                    )}
                                </FormItem>
                            </Col>
                            <Col span={9}>
                                <FormItem>
                                    {getFieldDecorator('allowDelete', {
                                        valuePropName: 'checked',
                                        initialValue: QMDetail['allowDelete'] ? true : false
                                    })(
                                        <Checkbox></Checkbox>
                                    )}
                                </FormItem>
                            </Col>
                        </Row>
                    </FormItem>
                    <FormItem label="">
                        {getFieldDecorator('sortCode', {
                            initialValue: QMDetail.sortCode,
                            rules: [
                                { required: true, message: '' },
                            ],
                        })(
                            <Input placeholder="" />
                        )}
                    </FormItem>
                </Form>
            </Modal>
        )
    }
}
export default Form.create()(categoryModal)